<template>
  <v-container>
    <h4 class="my-4">
      <span class="cblu bbred">All Deals</span>
    </h4>
    <v-row>
      <v-col cols="12" md="3">
        <!-- left sidebar -->
            <div class="mobile">
              <div class="d-flex justify-content-between">
                <v-btn small @click="price">Price</v-btn>
                <v-btn small @click="deals">Deals Near By</v-btn>
                <v-btn small @click="category">Change Category</v-btn>
              </div>
            </div>
            <div class="catside shadow desktop">
              <div class="h5 sec1">Price</div>
              <div class="range-slider sec1 bg-white p-2">
                <span class="d-flex">
                  <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                  <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
                </span>
                <v-range-slider v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
              </div>
              <div class="h5 sec1">Deals Near By</div>
              <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                <div class="text-center small">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
              <v-btn color="success" class="my-2">Go</v-btn>
            </div>
            <v-dialog v-model="dialog1" persistent>
              <v-card>
                <span class="d-flex justify-content-between">
                    <v-card-title>Price</v-card-title>
                    <v-icon @click="dialog1 = false" class="me-3">mdi-close</v-icon>
                </span>
                <hr class="m-0">
                <v-card-text>
                  <v-range-slider
                      v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
                      <span class="d-flex">
                        <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                        <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
                      </span>
                </v-card-text>
                <div class="text-center">
                  <b-button class="mb-3" variant="success" type="submit">Go</b-button>
                </div>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialog2" persistent>
              <v-card>
                <span class="d-flex justify-content-between">
                    <v-card-title>Deals Radar</v-card-title>
                    <v-icon @click="dialog2 = false" class="me-3">mdi-close</v-icon>
                </span>
                <hr class="m-0">
                <v-card-text>
                  <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                  <div class="text-center h6 link-dark">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
                </v-card-text>
                <div class="text-center">
                  <b-button class="mb-3" variant="success" type="submit">Go</b-button>
                </div>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialog3" persistent class="p-2">
              <v-card>
                <span class="d-flex justify-content-between">
                    <v-card-title>Change Category</v-card-title>
                    <v-icon @click="dialog3 = false" class="me-3">mdi-close</v-icon>
                </span>
                <hr class="m-0">
                <v-card-text class="pb-0">
                  <v-autocomplete solo dense :items="cats" label="Category" class="mt-3" placeholder="Category"></v-autocomplete>
                </v-card-text>
                <div class="text-center">
                  <b-button class="mb-3" variant="success" type="submit">Go</b-button>
                </div>
              </v-card>
            </v-dialog>
      </v-col>
      
      <v-col cols="12" md="9">
        <div class="mobile">
          <v-card v-for="(deal,i) in hotdeals" :key="i" class="item mitem my-2 mb-7">
            <div class="small d-flex">
              <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}">
                  <div class="mimg">
                      <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url!=null" :src=url+deal.hotdeal_single_image.hotdeal_img_url cover height="120"></v-img>
                      <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url==null" :src="('https://placehold.co/600x400?text='+deal.hot_deal_title)" cover height="120"></v-img>
                  </div>
              </router-link>
              <div class="mdetails">
                    <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}" class="mtitle text-decoration-none link-dark h4">
                        {{deal.hot_deal_title}}
                    </router-link>
                    <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                    <div class="cred">Price: ₹{{ deal.price_from }} - ₹{{ deal.price_to }}</div>
                    <div>Validity: {{ deal.date_from }}, {{ deal.date_from }}</div>
                    <div class="cblu">By: 
                      <router-link :to="{name:'businessprofile',params:{id:deal.user_id, uname:deal.username}}">
                        {{ deal.name }}
                      </router-link>
                    </div>
                    <div class="address">
                      <v-icon small>mdi-map-marker</v-icon>
                      <div v-if="deal.address!=null">{{ deal.address }}, </div>{{ deal.city }}, {{ deal.state }}
                    </div>
                    <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}" class="cred">Read More</router-link>
              </div>
              <div class="mcontact">
                <a :href="('https://wa.me/'+deal.mobile_phone+'?text=rf')" target="_blank">
                  <v-btn fab small color="success" class="mb-2"><v-icon>mdi-whatsapp</v-icon></v-btn>
                </a>
                <a :href="('tel:'+ deal.mobile_phone)">
                  <v-btn fab small color="primary" class="mb-2"><v-icon>mdi-phone</v-icon></v-btn>
                </a>
                <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}" class="cred">
                    <v-btn fab small color="info" class="me-1 mb-2">
                      <v-icon small>mdi-eye</v-icon>
                    </v-btn>
                </router-link>
              </div>
           </div>
            <v-row>
              <v-col md="3" style="font-size:10px">Hot deals[{{ deal.hotdealsCount }}]</v-col>
              <v-col md="3" style="font-size:10px">Sales[{{ deal.salesCount }}]</v-col>
              <v-col md="3" style="font-size:10px">Jobs[{{ deal.jobsCount }}]</v-col>
              <v-col md="3" style="font-size:10px">Videos[{{ deal.videosCount }}]</v-col>
            </v-row>
        </v-card>
      </div>
      <div class="desktop" v-if="rlists.length > 1">
            <v-card v-for="(deal,i) in rlists" :key="i" class="mb-7" :perPage="perPage">
              <v-row>
                <v-col md="4" class="py-0">
                  <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}">
                      <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url!=null" :src=url+deal.hotdeal_single_image.hotdeal_img_url cover height="200"></v-img>
                      <v-img v-if="deal.hotdeal_single_image.hotdeal_img_url==null" :src="('https://placehold.co/600x400?text='+deal.hot_deal_title)" cover height="200"></v-img>
                  </router-link>
                </v-col>
                <v-col cols="12" md="8" class="py-0">
                  <v-card-text>
                    <v-row>
                      <v-col cols="12" md="8">
                        <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}" class="text-decoration-none link-dark h4">
                          {{deal.hot_deal_title}}
                        </router-link>
                        <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                        <div class="cred">Price: ₹{{ deal.price_from }} - ₹{{ deal.price_to }}</div>
                        <div>Validity: {{ deal.date_from }}, {{ deal.date_from }}</div>
                      
                        <div class="cblu">By: 
                          <router-link :to="{name:'businessprofile',params:{id:deal.user_id, uname:deal.username}}">
                            {{ deal.name }}
                          </router-link>
                        </div>

                        <div class="address">
                          <v-icon small>mdi-map-marker</v-icon>
                            <div v-if="deal.address!=null">
                                {{ deal.address }}, 
                            </div>{{ deal.city }}, {{ deal.state }}

                          <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}" class="cred">Read More</router-link>
                        </div>
                        <v-row>
                            <v-col md="3" class="font-weight-bold">Hot deals[{{ deal.hotdealsCount }}]</v-col>
                            <v-col md="3" class="font-weight-bold">Sales[{{ deal.salesCount }}]</v-col>
                            <v-col md="3" class="font-weight-bold">Jobs[{{ deal.jobsCount }}]</v-col>
                            <v-col md="3" class="font-weight-bold">Videos[{{ deal.videosCount }}]</v-col>
                        </v-row>
                      
                      </v-col>
                      <v-col md="4">
                        <a :href="('tel:'+ deal.mobile_phone)">
                          <v-btn small color="primary" class="me-1 mb-2">
                            <v-icon small>mdi-phone</v-icon>Call Now</v-btn>
                        </a>

                        <a :href="('https://wa.me/'+deal.mobile_phone+'?text=rf')" target="_blank">
                          <v-btn small color="success" class="me-1 mb-2">
                            <v-icon small>mdi-whatsapp</v-icon>WhatsApp</v-btn>
                        </a>

                        <router-link :to="{name:'dealsdetail',params:{id:deal.hotdeal_id}}">
                          <v-btn small color="info" class="me-1 mb-2">
                            <v-icon small>mdi-deal</v-icon>View Deal</v-btn>
                        </router-link>
                      </v-col>
                    </v-row>
                    
                  </v-card-text>
                </v-col>
              </v-row>
            </v-card>
            <v-pagination v-if="rlists.length > 1" class="pagination" v-model="page" :length="Math.ceil(hotdeals.length/perPage)" circle></v-pagination>
      </div>
        
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';
import { EventBus } from "@/event-bus";
export default {
  name: "DailyDeals",
  data() {
    return {
      url: 'https://bizlx.itechvision.in',
      page:1,
      perPage:10,
      hotdeals: [],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      catvalue:null,
      selected: null,
      min: 0,
      max: 120000,
      price_from: [],
      price_to: [],
      range: [],
      range1: [0, 100],
      min1: 0,
      max1: 100,
      searchedCatData: []
    };
  },
  mounted(){
      EventBus.$on('event_bus_emitted', (data) =>{
          this.hotdeals = data.allhotDeals;
      });
      const config = {
          headers: { 'content-type': 'multipart/form-data' }
      }
      this.searchedCatData = JSON.parse(localStorage.getItem("catSearchedValues"));
      axios.post("search/category", this.searchedCatData, config)
          .then((resp) => {
            if(resp.data.dealsType=="hot deals"){ // deals page
                this.hotdeals = resp.data.allhotDeals;

                for(var k = 0; k < this.hotdeals.length; k++) { // salary
                  this.price_from.push(this.hotdeals[k].price_from);
                  this.price_to.push(this.hotdeals[k].price_to);
                }

                this.price_from = [...new Set(this.price_from.sort())];
                this.price_to = [...new Set(this.price_to.sort())];
                this.range = [this.price_from.slice(0)[0], this.price_to.slice(-1)[0]]; // min & max salary set

            }else{
              this.$router.push({name: 'home'});
            }
          },
      );
  },
  methods: {
    price() {
      this.dialog1 = true;
    },
    deals() {
      this.dialog2 = true;
    },
    category() {
      this.dialog3 = true;
    }
  },
  computed:{
    rlists(){
      return this.hotdeals.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    }
  },
}
</script>

<style scoped>
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 7px 0 0 7px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 10.4px;}
.catside{
  background: #e5e5e5;
  padding: 10px;
}
.sec1{
  background: #f2f2f2;
  padding: 5px 15px;
  margin-bottom: 20px;
}
</style>